import React, { useState, useEffect, Fragment } from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadingPlanets, saveCurrentPlanet } from '../redux/actions';
import List from '@material-ui/core/List';
import { getUrl } from '../../utils/index';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import './Planets.scss'

function Planets(props) {

  const [loading, setLoading] = useState(props.needPlanets !== null)

  useEffect(() => {
    async function fetchData() {

      let requests = props.urls.map(url => fetch(url));

      await Promise.all(requests)
        .then(async responses => await Promise.all(responses.map(r => r.json())))
        .then(planets => props.savePlanets(planets))

      setLoading(false);
    };

    if (props.needPlanets) {
      fetchData();
    }

  }, []);

  return (
    <>
      {loading ? (
        <CircularProgress color="secondary" size={50} />
      ) : (
          <>
            <List className="list" component="ul" aria-label="secondary mailbox folders">
              <h3>PLANETS</h3>
              {props.planets.map((planet, index) =>
                <Fragment key={index}>
                  <ListItem button>
                    <ListItemText
                      onClick={() => {
                        props.history.push('/planets/' + getUrl(props.planets[index].url))
                        props.saveCurrentPlanet(planet)
                      }}
                      primary={planet.name}
                    />
                  </ListItem>
                  <Divider light={true} />
                </Fragment>
              )}
            </List>
          </>
        )
      }
    </>
  );
}

function mapStateToProps(state) {
  return {
    urls: state.needPlanets,
    planets: state.showPlanets,
    needPlanets: state.needPlanets
  }
}

function mapDispatchToProps(dispatch) {
  return {
    savePlanets: (data) => dispatch(loadingPlanets(data)),
    saveCurrentPlanet: (data) => dispatch(saveCurrentPlanet(data)),
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Planets));